import * as Yup from 'yup';
import { useEffect, useState, } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// swal
import swal from 'sweetalert';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Alert, Typography } from '@mui/material';
import axios from '../../../../utils/axios';
import { FormProvider, RHFSelect, RHFTextField, RHFSwitch } from '../../../../components/hook-form';

// format number 
import { fCurrency, } from '../../../../utils/formatNumber';

import useAuth from '../../../../hooks/useAuth';
import { PATH_DASHBOARD } from '../../../../routes/paths';

// ----------------------------------------------------------------------

export default function PurchaseBill() {

  const NewUserSchema = Yup.object().shape({
    meter_number: Yup.string().required('Meter  Number Required'),
    disco: Yup.string().required('Disco Name Required'),
    meter_type: Yup.string().required('Meter Type Required')
  });
  const pinSchema = Yup.object().shape({
    meter_number: Yup.string().required('Meter Number Required'),
    disco: Yup.string().required('Disco Name Required'),
    meter_type: Yup.string().required('Meter Type Required'),
    pin: Yup.string().min(4, 'Transaction Pin Must Be 4 Digits').max(4, 'Transaction Pin Must Be 4 Digit').required('Transaction Pin Required'),

  });
  const { enqueueSnackbar } = useSnackbar();
  const [disco, setDisco] = useState()
  const [customername, Setcustomer] = useState()
  const [amount, SetAmount] = useState();
  const AccessToken = window.localStorage.getItem('accessToken');
  const { setting } = useAuth();
  const navigate = useNavigate();
  const pin = setting?.setting?.allow_pin === 1;
  const methods = useForm({
    resolver: yupResolver(pin ? pinSchema : NewUserSchema),
  });
  const [discount, SetDiscount] = useState();
  const {
    setValue,
    watch,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;


  useEffect(() => {
    axios.get('/api/website/app/bill/list').then(data => {
      setDisco(data.data?.bill);
    });
  }, []);
  const values = watch()
  const bypass = values?.bypass;

  const onSubmit = async (data) => {
    const value = disco.filter((item) => item.plan_id === data.disco)
    let utilityCompany = '';
    if (value[0]) {
      utilityCompany = value[0].disco_name
    }
    try {
      const isConfirmed = await swal({
        title: 'Confirmation',
        text: `You are about to pay ${amount} for electricity to the ${utilityCompany} meter number ${data.meter_number}.`,
        icon: 'warning',
        buttons: ['Cancel', 'Confirm'],
      });
      if (isConfirmed) {
        const resp = await axios.post('/api/bill', {
          disco: data.disco,
          meter_type: data.meter_type,
          meter_number: data.meter_number,
          amount,
          bypass: data?.bypass || false,
          pin: data.pin,
          token: AccessToken,
        })
        if (resp?.data?.status === 'success') {
          swal({
            title: 'success', text: `${resp?.data?.message}`, icon: 'success', buttons: [
              'Okay',
              'Invoice'
            ],
            dangerMode: true,
          }).then((isConfirm) => {
            if (isConfirm) {
              navigate(`${PATH_DASHBOARD.general.invioce}/${resp?.data?.transid}/bill`, { replace: true });
            }
            window.location.reload();
          });
        } else if (resp?.data?.status === 'fail') {
          swal('Opps', `${resp?.data?.message}`, 'error');
        } else {
          swal('Info', `${resp?.data?.message}`, 'info');
        }
      }
    } catch (error) {
      if (error?.data?.message !== undefined) {
        swal('Opps', `${error?.data?.message}`, 'error');
      } else if (error?.message !== undefined) {
        swal('Opps', `${error?.message}`, 'error');
      } else {
        swal('Opps', 'Something went wrong', 'error');
      }
    }
  };


  const Calculate = async (data) => {
    SetAmount(data.target.value)
    axios.get(`/api/bill/charges/${data.target.value}/admin`).then(data => {
      SetDiscount(`₦${fCurrency(data?.data?.charges)}`);
    }).catch((error) => {
      SetDiscount(undefined);
      SetAmount(undefined)
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    })
  }
  const verify = async (data) => {
    try {
      const resp = await axios.get(`/api/bill/bill-validation?meter_type=${data.meter_type}&meter_number=${data.meter_number}&disco=${data.disco}`)
      setValue('bypass', true)
      Setcustomer(resp?.data?.name)

    } catch (error) {
      Setcustomer(undefined)
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(bypass ? onSubmit : verify)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 1,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            >
              <RHFSelect name="disco" label="Disco Name">
                <option value="" />
                {disco?.map((option) => (
                  <option key={option?.plan_id} value={option?.plan_id}>
                    {option?.disco_name}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect name="meter_type" label="Meter Type">
                <option value="" />
                <option value="prepaid" >Prepaid </ option>
                <option value="postpaid" >Postpaid </ option>
              </RHFSelect>
              <RHFTextField name="meter_number" label="Meter Number" />

              {pin && <RHFTextField name="pin" label="Transaction Pin" type="password" autoComplete="new-password" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />}
              <RHFTextField name="amount" label="Amount" value={amount} onChange={Calculate} />
              {discount !== undefined && <RHFTextField name="charges" label="Charges" value={discount} disabled />}
              {customername !== undefined && <RHFTextField name="customername" label="customer name" value={customername} disabled />}
              <RHFSwitch
                name='bypass'
                labelPlacement="start"
                def={false}
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Bypass Meter Number
                    </Typography>
                  </>
                }
                sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
              />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                {bypass ? 'Purchase' : 'Verify Meter Number'}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
